import React, { useEffect, useState } from 'react';
import '../style/fanletter.css';

const Fanletter = () => {
  const [fanletters, setFanletters] = useState([]);
  const [newFanletter, setNewFanletter] = useState("");
  const [editingFanletter, setEditingFanletter] = useState(null);
  const [editingText, setEditingText] = useState("");

  useEffect(() => {
    // 팬레터 데이터를 가져오는 로직 (예시로 하드코딩된 데이터 사용)
    const fetchedFanletters = [
      { id: 1, text: "샤이니 사랑해요!" },
      { id: 2, text: "늘 응원합니다!" },
    ];
    setFanletters(fetchedFanletters);
  }, []);

  const handleAddFanletter = () => {
    if (newFanletter.trim() === "") return;
    const newLetter = { id: Date.now(), text: newFanletter };
    setFanletters([...fanletters, newLetter]);
    setNewFanletter("");
  };

  const handleEditFanletter = (id) => {
    const letter = fanletters.find((letter) => letter.id === id);
    setEditingFanletter(id);
    setEditingText(letter.text);
  };

  const handleUpdateFanletter = (id) => {
    const updatedLetters = fanletters.map((letter) =>
      letter.id === id ? { ...letter, text: editingText } : letter
    );
    setFanletters(updatedLetters);
    setEditingFanletter(null);
    setEditingText("");
  };

  const handleDeleteFanletter = (id) => {
    const updatedLetters = fanletters.filter((letter) => letter.id !== id);
    setFanletters(updatedLetters);
  };

  return (
    <div className="my-5">
      <h1 className="fanletter-header my-5">Fan letter</h1>
      <div className="fanletter-container">
      <p>아티스트에게 응원메시지를 전달해보세요!</p>
      <div className="fanletter-form">
        <input
          type="text"
          value={newFanletter}
          onChange={(e) => setNewFanletter(e.target.value)}
          placeholder="팬레터 작성"
        />
        <button onClick={handleAddFanletter}>전달하기♡</button>
      </div>
      <ul className="fanletter-list">
        {fanletters.map((letter) => (
          <li key={letter.id} className="fanletter-item">
            {editingFanletter === letter.id ? (
              <>
                <input
                  type="text"
                  value={editingText}
                  onChange={(e) => setEditingText(e.target.value)}
                />
                <button onClick={() => handleUpdateFanletter(letter.id)}>저장</button>
              </>
            ) : (
              <>
                <span>{letter.text}</span>
                <div>
                  <button onClick={() => handleEditFanletter(letter.id)}>수정</button>
                  <button className="delete-button" onClick={() => handleDeleteFanletter(letter.id)}>삭제</button>
                </div>
              </>
            )}
          </li>
        ))}
      </ul>
    </div>
    </div>
  );
};

export default Fanletter;
