import React from 'react';
import '../layouts/footer.css';

function Footer() {
  return (
    <footer className="footer">
      <div className="container">
        <div className="footer-content">
          <div className="footer-logo">
          <img src="https://raw.githubusercontent.com/SSuaSea/shinee-fansite/46008e8d6aec1dc854835e3256b652933dfb25ed/logo.svg" alt="logo" />
          </div>
          <div className="footer-links">
            <ul>
              <li><a href="https://github.com/SSuaSea/shinee-fansite">Fansite Github</a></li>
              <li><a href="https://github.com/SSuaSea">GitHub</a></li>
              <li><a href="https://velog.io/@suasea_77777/posts">Blog</a></li>
            </ul>
          </div>
          <div className="footer-social">
            <a href="https://www.facebook.com" target="_blank" rel="noopener noreferrer">
              <i className="fab fa-facebook-f"></i>
            </a>
            <a href="https://www.twitter.com" target="_blank" rel="noopener noreferrer">
              <i className="fab fa-twitter"></i>
            </a>
            <a href="https://www.instagram.com" target="_blank" rel="noopener noreferrer">
              <i className="fab fa-instagram"></i>
            </a>
            <a href="https://www.linkedin.com" target="_blank" rel="noopener noreferrer">
              <i className="fab fa-linkedin-in"></i>
            </a>
          </div>
        </div>
      </div>
    </footer>
  );
}

export default Footer;
